.background-cover() {
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
}
.clearfix() {
  &:after {
    display: block;
    content: "";
    clear: both;
  }
}
.parallax() {
  background-size: cover;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
}