#main{
    
    input::-webkit-input-placeholder {
        text-indent: 0px;
        transition: text-indent 0.3s ease;
    }
    textarea::-webkit-input-placeholder {
        opacity: 1;
        transition: 0.3s ease;
    }
    input::-moz-placeholder {
        text-indent: 0px;
        transition: text-indent 0.3s ease;
    }
    textarea::-moz-placeholder {
        opacity: 1;
        transition: 0.3s ease;
    }
    input:-ms-input-placeholder {
        text-indent: 0px;
        transition: text-indent 0.3s ease;
    }
    textarea::-ms-input-placeholder {
        opacity: 1;
        transition: 0.3s ease;
    }
    input::placeholder {
        text-indent: 0px;
        transition: text-indent 0.3s ease;
    }
    textarea::placeholder {
        opacity: 1;
        transition: 0.3s ease;
    }
    input:focus::-webkit-input-placeholder {
        text-indent: 100%;
        transition: text-indent 0.3s ease;
    }
    textarea:focus::-webkit-input-placeholder {
        opacity: 0;
        transition: 0.3s ease;
    }
    input:focus::-moz-placeholder {
        text-indent: 100%;
        transition: text-indent 0.3s ease;
    }
    textarea:focus::-moz-placeholder {
        opacity: 0;
        transition: 0.3s ease;
    }
    input:focus:-ms-input-placeholder {
        text-indent: 100%;
        transition: text-indent 0.3s ease;
    }
    textarea:focus::-ms-input-placeholder {
        opacity: 0;
        transition: 0.3s ease;
    }
    .button-def{
        font-family: @font-family-bold;
        border: none;
        background: none;
        text-transform: uppercase;
    }
    .btn-green{
        color: #505251;
        border: 3px solid #6cffb6;
        width: 215px;
        text-align: center;
        padding: 15px 0;
        font-size: 12px;
        border-radius: 10px;
        transition: .3s;
        &:hover{
            transition: .3s;
            background: #6cffb6;
            color:#fff !important;
        }
    }
    .btn-bckgrd{
        padding: 15px 45px;
        text-align: center;
        border-radius: 3px;
        color: #fff;
    }
    .btn-round{
        color: #fff;
        width: 200px;
        border-radius: 30px;
    }

    .top-logo{
        .container{
            text-align: center;
            padding-top: 15px;
            padding-bottom: 15px;
        }
        .row{
            @media (min-width: 767px){
                .flex-display(flex);
                .align-items(center);
            } 
        }
        .top-half-blocks{
            text-align: center;
            background: #0ab96d;
            background: -moz-linear-gradient(left, #0ab96d 50%, #47ca95 50%);
            background: -webkit-linear-gradient(left, #0ab96d 50%,#47ca95 50%);
            background: linear-gradient(to right, #0ab96d 50%,#47ca95 50%);
            filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#0ab96d', endColorstr='#47ca95',GradientType=1 );
            color: #fff;
            font-size: 12px;
            font-family: @font-family-bold;
            text-transform: uppercase;
            @media (max-width: 767px){
                background: -moz-linear-gradient(top, #0ab96d 50%, #47ca95 50%);
                background: -webkit-linear-gradient(top, #0ab96d 50%,#47ca95 50%);
                background: linear-gradient(to bottom, #0ab96d 50%,#47ca95 50%);
            } 
            .left{
                @media (max-width: 767px){
                    margin-bottom: 30px;
                } 
            }
        }
        img{
            @media (max-width: 767px){
                margin: 20px 0;
            } 
        }
    }

    .header-paralax{
        background: url('../images/header.jpg') no-repeat;
        background-size: cover;
        background-attachment: fixed;
        padding: 140px 0;
        @media (max-width: 992px){
            padding: 90px 0;
            @media (max-width: 767px){
                padding: 40px 0;
            } 
        } 
        .header-title{
            font-size: 26px;
            color: #fff;
            text-transform: uppercase;
            margin-bottom: 35px;
            @media (max-width: 767px){
                margin-bottom: 20px;
                font-size: 22px;
            }
        }
        .header-text{
            font-size: 16px;
            color: #fff;
            margin-bottom: 35px;
            @media (max-width: 767px){
                margin-bottom: 20px;
                font-size: 14px;
            }
        }
        .btn-bckgrd{
            background: #c7ba85;
            transition: .3s;
            &:hover{
                transition: .3s;
                background: darken(#c7ba85, 10%);
            }
            @media (max-width: 767px){
                width: 100%;
            }
        }
    }

    .clients{
        background: #f3f3f5;
        padding: 25px 0;
        .client{
            display: inline-block;
            margin-right: 9.2%;
            &:last-child{
                margin: 0;   
            }
            @media (max-width: 1200px){
               margin-right: 4.2%;
                @media (max-width: 992px){
                    margin: 0 40px 35px 0;
                    @media (max-width: 767px){
                        display: block;
                        margin: 0 0 25px;
                        img{
                            display: block;
                            margin: 0 auto;
                        }
                    }
                }
            }
        }
    }
    .info-block{
        padding: 60px 0 80px;
        text-align: center;
        line-height: 1;
        @media (max-width: 767px){
            padding: 40px 0 50px;
        }
        h2{
            font-size: 18px;
            color: #adadad;
            font-family: @font-family-bold;
            margin: 0 0 75px 0;
            text-transform: uppercase;
            @media (max-width: 767px){
                margin: 0 0 35px 0;
            }
        }
        .info-text{
            font-size: 32px;
            color: #555;
            margin: 0 0 55px;
            line-height: 1.3;
            @media (max-width: 767px){
                margin: 0 0 35px;
                font-size: 22px;
            }
            span{
                color: #3a2489;
            }
        }
        .small-text{
            font-size: 12px;
            line-height: 1;
            .line{
                margin-bottom: 10px;
                @media (max-width: 767px){
                    margin-bottom: 5px;
                }
            }
        }
        .btn-bckgrd{
            background: #6c3778;
            transition: .3s;
            &:hover{
                transition: .3s;
                background: darken(#6c3778, 10%);
            }
            @media (max-width: 767px){
                width: 100%;
            }
        }
    }
    .our-team{
