                    font-size: 25px;
                }
            }
        }
        .image{
            background: #fff;
            width: 120px;
            height: 120px;
            border: 5px solid #6cffb6;
            border-radius: 100%;
            margin: 0 auto 25px;
            overflow: hidden;
            img{
                width: 100%;
                height: auto;
                border-radius: 100%;
            }
            @media (max-width: 767px){
                margin: 0 auto 15px;
            }
        }
        .position{
            color: #fff;
            font-size: 14px;
            font-family: @font-family-bold;
            margin: 0 0 25px 0;
