                margin: 0 0 15px;
            }
        }
        .description{
            margin: 0 auto;
            width: 90%;
            @media (max-width: 767px){
                width: 100%;
            }
            p{
                color: #fff;
                font-size: 11px;
                margin-bottom: 15px;
                @media (max-width: 767px){
                    margin-bottom: 5px;
                }
                &:last-child{
                    margin-bottom: 50px;
                    @media (max-width: 767px){
                        margin-bottom: 30px;
                    }
                }
            }
        }
        .row-flex{
            @media (min-width: 767px){
                .flex-display(flex);
                .align-items(stretch);
            }
        }
        .last-btn{
            @media (min-width: 767px){
                position: absolute;
                bottom: 0;
                margin: 0 auto;
                display: block;
                left: 50%;
                transform: translate(-50%, 0%);
            }
        }
        .btn-green{
            @media (max-width: 767px){
                width: 100%;
            }
        }
        .col-sm-4{
            @media (max-width: 767px){
