    .big-foto-block{
        max-width: 1200px;
        margin: 0 auto;
        @media (max-width: 767px){
            padding: 30px 15px;
            text-align: center;
            width: 100%;
        }
        .big-foto-wrap{
            @media (min-width: 767px){
                display: flex;
            }
        }
        .big-foto{
            width: 50%;
            img{
                width: 100%;
                height: auto;
            }
            @media (max-width: 767px){
                display: none;
            }
        }
        .big-desc{
            width: 50%;
            padding: 0 140px 0 70px;
            @media (max-width: 992px){
                padding: 0 30px;
                @media (max-width: 767px){
                    width: 100%;
                    padding: 0;
                }
            }
            h4{
                padding: 60px 0 30px;
                color: #adadad;
                font-size: 12px;
                @media (max-width: 1200px){
                    padding: 30px 0;
                    @media (max-width: 992px){
                        padding: 20px 0;
                        @media (max-width: 767px){
                            padding: 10px 0 30px 0;
                        }
                    }
                } 
            }
            .quote{
                font-size: 27px;
                color: #555;
                line-height: 1.3;
                margin-bottom: 50px;
                @media (max-width: 1200px){
                    font-size: 22px;
                    line-height: 1.1;
                    margin-bottom: 30px;
                    @media (max-width: 992px){
                        margin-bottom: 20px;
                        font-size: 20px;
                    }
                }
            }
            .quote-small{
                font-size: 11px;
                color: #555;
                margin-bottom: 50px;
                @media (max-width: 992px){
                    margin-bottom: 25px;
                }
            }
            .btn-round{
                color: #505251;
            }
        }
        .btn-green{
            @media (max-width: 767px){
                width: 100%;
            }
        }
        .img-right{
            .big-desc{
                .quote{
                    padding: 60px 0 0 0;
                    @media (max-width: 992px){
                        padding: 30px 0 0;
                        @media (max-width: 767px){
                            padding: 40px 0 0;
                        }
                    }
                }
                .quote-small{
                    margin-bottom: 100px;
                    @media (max-width: 992px){
                        margin-bottom: 60px;
                        @media (max-width: 767px){
                            margin-bottom: 25px;
                        }
                    }
                }
            }
        }
    }
    .numbers{
        padding: 50px 0 60px;
        background: url('../images/parallax.jpg') no-repeat;
