        }
        .big-text{
            color: #092f44;
            font-size: 28px;
            width: 700px;
            line-height: 1.3;
            margin: 0 auto 45px;
            text-align: center;
            @media (max-width: 992px){
                width: 100%;
                @media (max-width: 767px){
                    font-size: 22px;
                    margin-bottom: 30px;
                }
            }
            span{
                color: #8e6597;
            }
        }
        .btn-bckgrd{
            background: #2add81;
            width: 270px;
