    .services{
        background: #212121;
        padding: 100px 0;
        @media (max-width: 992px){
            padding: 50px 0 20px 0;
        }
        h5{
            color: #fff;
            font-family: @font-family-bold;
            font-size: 18px;
            padding-bottom: 45px;
            margin-bottom: 40px;
            position: relative;
            text-transform: uppercase;
            text-align: center;
            @media (max-width: 767px){
                padding-bottom: 25px;
                margin-bottom: 20px;
            }
            &:after{
                content: '';
                width: 45px;
                height: 2px;
                background: #40905c;
                position: absolute;
                left: 50%;
                bottom: 0%;
                transform: translate(-50%, 0%);
            }
        }
        .text{
            font-size: 18px;
            color: #fff;
            width: 710px;
            text-align: center;
            margin: 0 auto 65px;
            @media (max-width: 767px){
                width: 100%;
                margin-bottom: 30px;
            }
        }
        .green-text{
            color: #25c64d;
            text-align: center;
            font-size: 14px;
            margin-bottom: 100px;
            @media (max-width: 767px){
                margin-bottom: 40px;
            }
        }
        .row-flex{
            @media (min-width: 767px){
                .flex-display(flex);
                .align-items(stretch);
            }
        }
        .service{
            font-family: @font-family-bold;
            color: #489dff;
            font-size: 14px;
            text-transform: uppercase;
            margin-bottom: 20px;
            @media (max-width: 767px){
                font-size: 20px;
                margin: 0;
            }
        }
        .help{
            font-size: 36px;
            color: #fff;
            margin-bottom: 25px;
            @media (max-width: 767px){
                font-size: 26px;
                margin-bottom: 15px;
            }
        }
        .serv{
            position: relative;
            padding: 0 15px 70px 115px;
            background: url('../images/icon_assistant.png') no-repeat;
            background-repeat: no-repeat;
            background-size: 90px;
            background-position: 0px 5px;
            @media (max-width: 992px){
                padding: 0 15px 70px 65px;
                background-size: 60px;
                margin-bottom: 50px;
                @media (max-width: 767px){
                    background: none !important;
                    text-align: center;
                    padding: 0 15px;
                }
            }
            &.yellow-serv{
                background-image: url('../images/icon_bookkeepers.png');
                .service{
                    color: #f7cb44;
                }
                a{
                    color: #f7cb44;
                }
            }
            &.grey-serv{
                background-image: url('../images/icon_support.png');
                .service{
                    color: #c6b987;
                }
                a{
                    color: #c6b987;
                }
            }
            &.purple-serv{
                background-image: url('../images/icon_bookkeeping.png');
                .service{
                    color: #6d427e;
                }
                a{
                    color: #6d427e;
                }
            }
            a{
                font-size: 18px;
                color: #489dff;
                border-bottom: 3px solid;
                text-decoration: none;
                position: absolute;
                bottom: 0;
                @media (max-width: 767px){
                    position: static;
                    margin: 15px 0 0;
                    display: inline-block;
                }
