        }
    }
    .client-form{
        padding: 100px 0 110px 0;
        background: url('../images/form-bg.jpg') no-repeat;
        background-size: cover;
        background-attachment: fixed;
        @media (max-width: 992px){
            padding: 50px 0 60px;
        }
        .top-text{
            color: #fff;
            text-align: center;
        }
        .love-text{
            font-size: 36px;
            margin-bottom: 40px;
            img{
                margin: 0 20px;
                @media (max-width: 992px){
                    margin: 0 10px;
                    width: 40px;
                }
            }
            @media (max-width: 992px){
                font-size: 26px;
                margin-bottom: 20px;
            }
        }
        .details{
            font-size: 23px;
            margin-bottom: 45px;
            @media (max-width: 992px){
                font-size: 20px;
                margin-bottom: 30px;
            }
        }
        .form{
            width: 860px;
            margin: 0 auto;
            @media (max-width: 992px){
                width: 100%;
            }
        }
        .text-input,
        select{
            width: 100%;
            border: none;
            color: #a9a9a9;
            background: #fff;
            border-radius: 3px;
            padding: 20px 15px;
            font-size: 18px;
            margin-bottom: 15px;
            @media (max-width: 767px){
                padding: 15px 10px;
                font-size: 15px;
            }
        }
        select{
            cursor: pointer;
            -webkit-appearance: none;
            -o-appearance: none;
            -moz-appearance: none;
            -ms-appearance: none;
            appearance: none;
            background: #fff url('../images/arrow.png') no-repeat;
            background-position: 95% center;
            margin: 0 0 25px 0;
            @media (max-width: 767px){
                margin-bottom: 15px;
            }
        }
        .check-input{
            display: none;
        }
        .check-label{
            color: #fff;
            font-size: 18px;
            font-weight: normal;
            margin-bottom: 15px;
            cursor: pointer;
            &:before{
                content: '';
                display: inline-block;
                vertical-align: middle;
                margin-right: 15px;
                width: 39px;
                height: 39px;
                background: url('../images/box.png') no-repeat;
                background-position: center;
            }
        }
        .check-input:checked + label{
            &:before{
                content: '';             
                background: url('../images/box-check.png') no-repeat;
                background-position: center;
            }
        }
        .btn-bckgrd{
            width: 100%;
            background: #35df87;
            font-size: 18px;
            transition: .3s;
            padding: 20px 45px;
            border: none;
            &:hover{
                transition: .3s;
                background: darken(#35df87, 10%);
            }
            @media (max-width: 767px){
                padding: 13px 45px;
            }
        }
    }
    .above-footer{
        padding: 45px 0 80px;
        background: #373a3c;
        @media (max-width: 992px){
            padding: 25px 0 30px;
        }
        .logo-text{
            font-family: @font-family-bold;
            color: #fff;
            font-size: 18px;
        }
        .logo-block{
            text-align: center;
            @media (max-width: 767px){
                margin-bottom: 20px;
            }
            img{
                max-width: 100%;
                height: 120px;
            }
        }
        ul{
            margin: 0;
            padding: 0;
            @media (min-width: 992px){
                .flex-display(flex);
                .justify-content(space-between);
            }
            @media (max-width: 767px){
                text-align: center;
                margin-bottom: 20px;
            }
            li{
                @media (max-width: 767px){
                    margin-bottom: 10px;
                }
            }
            a{
                font-size: 14px;
                color: #fff;
                text-transform: uppercase;
            }
        }
        .inform-block{
            @media (max-width: 767px){
                text-align: center;
            }
        }
        .email,
        .phone{
            font-size: 14px;
            color: #fff;
            margin-bottom: 5px;
            word-break: break-all;
        }
        .phone{
            margin: 0;
        }
        .btn-white{
            margin: 20px 0;
            width: 190px;
            border: 3px solid #fff;
            border-radius: 3px;
            color: #fff;
            padding: 10px 0;
            text-align: center;
            font-size: 10px;
            transition: .3s;
            &:hover{
                transition: .3s;
                background: #fff;
                color: #373a3c;
            }
            @media (max-width: 992px){
                width: 100%;
            }
            @media (max-width: 767px){
                width: 250px;
            }
            @media (max-width: 480px){
                width: 100%;
            }
        }
        .info{
            font-size: 11px;
            color: #818890;
            line-height: 1.8;
        }
    }
    footer{
        padding: 20px 0;
        background: #262626;
        overflow: hidden;
        .left-block{
            float: left;
            color: #525252;
            font-size: 11px;
            padding: 5px 0 0 0;
            @media (max-width: 767px){
                float: none;
                text-align: center;
            }
        }
        .right-block{
            float: right;
            @media (max-width: 767px){
                float: none;
                text-align: center;
                margin-bottom: 15px;
            }
        }
        ul{
            margin: 0;
            padding: 0;
            @media (min-width: 767px){
                .flex-display(flex);
                .align-items(center);
            }
            li{
                margin-right: 55px;
                transition: .3s;
                @media (max-width: 992px){
                    margin-right: 30px;
                    @media (max-width: 767px){
                        display: inline-block;
                        @media (max-width: 480px){
                            margin-right: 15px;
                        }
                    }
                }
                &:last-child{
                    margin: 0;
                }
                &:hover{
                    transition: .3s;
                    transform: rotate(360deg);
                }
            }
        }
    }
}
.StripeElement {
    background-color: white;
    height: 40px;
    padding: 10px 12px;
    border-radius: 4px;
    border: 1px solid transparent;
    box-shadow: 0 1px 3px 0 #e6ebf1;
    -webkit-transition: box-shadow 150ms ease;
    transition: box-shadow 150ms ease;
}

.StripeElement--focus {
    box-shadow: 0 1px 3px 0 #cfd7df;
}

.StripeElement--invalid {
    border-color: #fa755a;
}

.StripeElement--webkit-autofill {
    background-color: #fefde5 !important;
}